import React, { useState, useEffect } from 'react';
import { Line } from 'react-chartjs-2';
import { Chart as ChartJS, CategoryScale, LinearScale, PointElement, LineElement, Title, Tooltip, Legend, Filler } from 'chart.js';
import { getHistoricalData } from '../../apiConfig';

ChartJS.register(CategoryScale, LinearScale, PointElement, LineElement, Title, Tooltip, Legend, Filler);

// --- (No change to Skeleton) ---
const ChartSkeleton = ({ chartTitle, ticker }) => (
    <div className="p-4 w-full h-full animate-pulse">
        <p className="font-semibold text-sm text-center text-gray-400 mb-4">{chartTitle || ticker}</p>
        <div className="bg-gray-100 rounded-md w-full h-4/5">
            <div className="bg-gray-200/75 rounded-md w-full h-full"></div>
        </div>
    </div>
);

const MarketChart = ({ ticker, chartTitle }) => {
    const [chartData, setChartData] = useState(null);
    const [loading, setLoading] = useState(true);
    const [error, setError] = useState(null);

    useEffect(() => {
        if (!ticker) return;

        const fetchChartData = async () => {
            setLoading(true);
            setError(null);

            try {
                const response = await getHistoricalData(ticker);
                const timeSeries = response.data ? response.data['Time Series (Daily)'] : null;

                if (timeSeries) {
                    const dates = Object.keys(timeSeries).slice(0, 30).reverse();
                    const prices = dates.map(date => parseFloat(timeSeries[date]['4. close']));

                    if (dates.length === 0) {
                        setError(`No historical data points found for ${ticker}.`);
                        return;
                    }

                    const formattedDates = dates.map(date => new Date(date).toLocaleDateString('en-US', { month: 'short', day: 'numeric' }));

                    setChartData({
                        labels: formattedDates,
                        datasets: [
                            {
                                label: `Price ($)`,
                                data: prices,
                                borderColor: '#2563eb',
                                backgroundColor: (context) => {
                                    const ctx = context.chart.ctx;
                                    const gradient = ctx.createLinearGradient(0, 0, 0, 200);
                                    // --- MODIFIED: Slightly stronger gradient start for TradingView feel ---
                                    gradient.addColorStop(0, 'rgba(37, 99, 235, 0.2)'); // Was 0.1
                                    gradient.addColorStop(1, 'rgba(37, 99, 235, 0)');
                                    return gradient;
                                },
                                borderWidth: 2,
                                fill: true,
                                tension: 0.4,
                                pointRadius: 0,
                                // --- MODIFIED: Removed hover point for a cleaner crosshair look ---
                                pointHoverRadius: 0,
                                // --- (pointBackgroundColor removed as hover radius is 0) ---
                            },
                        ],
                    });
                } else {
                    setError(response.data.message || `Invalid data format received for ${ticker}.`);
                }

            } catch (err) {
                if (err.response && err.response.data && err.response.data.message) {
                    setError(err.response.data.message);
                } else {
                    setError('Could not connect to the API. It may be temporarily unavailable.');
                }
                console.error("Failed to fetch chart data:", err);
            } finally {
                setLoading(false);
            }
        };

        fetchChartData();
    }, [ticker]);

    if (loading) {
        return <ChartSkeleton chartTitle={chartTitle} ticker={ticker} />;
    }

    if (error) {
        return (
            <div className="p-4 text-center">
                <p className="font-semibold text-sm text-gray-800">{chartTitle || ticker}</p>
                <div className="text-red-600 text-xs font-semibold mt-2 break-words">{error}</div>
            </div>
        );
    }

    const options = {
        responsive: true,
        maintainAspectRatio: false,
        plugins: {
            legend: { display: false },
            title: {
                display: true,
                text: `${chartTitle || ticker}`,
                font: { size: 16, weight: '600' },
                color: '#111827',
                padding: { top: 10, bottom: 20 }
            },
            tooltip: {
                enabled: true,
                mode: 'index',
                intersect: false,
                backgroundColor: '#111827',
                titleColor: '#ffffff',
                bodyColor: '#ffffff',
                borderColor: '#374151',
                borderWidth: 1,
                cornerRadius: 4,
                displayColors: false,
                padding: 10,
                callbacks: {
                    // --- MODIFIED: Minimalist tooltip, shows only the price value ---
                    label: (context) => `$${context.parsed.y.toFixed(2)}`
                }
            }
        },
        scales: {
            x: {
                display: true,
                title: {
                    display: false,
                },
                ticks: {
                    display: true,
                    font: { size: 10 },
                    color: '#4b5563',
                    maxTicksLimit: 6
                },
                grid: { display: false }
            },
            y: {
                display: true,
                title: {
                    display: false,
                },
                ticks: {
                    font: { size: 10 },
                    color: '#4b5563',
                    callback: (value) => `$${value.toFixed(0)}`,
                    // --- ADDED: Limit number of Y-axis ticks for a cleaner look ---
                    maxTicksLimit: 5
                },
                grid: {
                    // --- MODIFIED: Very light, dashed gridlines for TradingView look ---
                    color: '#f9fafb', // Lighter than before
                    drawBorder: false,
                    borderDash: [2, 4], // Dashed lines
                }
            }
        }
    };

    return (
        <div className="p-4 w-full h-full">
            {chartData ? <Line options={options} data={chartData} /> : null}
        </div>
    );
};

export default MarketChart;